.nav__list {
  .ul();

  @media (min-width: @tablet-width) {
    background-image: linear-gradient(to bottom, @color-bg 89px, rgba(@color-title, 0.1) 90px, @color-bg2 45.5px);
  }

  @media (min-width: @desktop-width) {
    background-image: linear-gradient(to bottom, @color-bg 89px, rgba(@color-title, 0.1) 90px, @color-bg2 45.5px);
  }

  &--inner-page {
    @media (min-width: @tablet-width) {
      background-image: linear-gradient(to bottom, @color-bg 89px, rgba(@color-title, 0.1) 90px, @color-main2 45.5px);
    }

    @media (min-width: @desktop-width) {
      background-image: linear-gradient(to bottom, @color-bg 89px, rgba(@color-title, 0.1) 90px, @color-main2 45.5px);
    }
  }
}

.nav__list--closed .nav__item:not(.nav__item--logo) {
  @media (max-width: 767px) {
    display: none;
  }
}

.nav__list--open .nav__item:not(.nav__item--desktop, .nav__item--tablet) {
  display: block;
}

.nav__item--tablet,
.nav__item--desktop {
  display: none;
}

.nav__item {
  box-shadow: 0 1px 0 @color-border2, inset 0 1px 0 @color-border2;
  background: @color-bg;

  &--search {
    @media (min-width: @tablet-width) {
      width: 82px;
      padding: 0;
    }
  }
}

.nav__logo {
  display: flex;
  flex-direction: column;
  margin-left: auto;
  margin-right: auto;

  @media (min-width: @tablet-width) {
    margin-left: 0;
    width: 113px;
    height: 45px;
  }

  @media (min-width: @desktop-width) {
    margin-left: 37px;
    width: 150px;
    height: 72px;
  }
}

.nav__link {
  .text(17px, 24px, @color-title, bold);
  padding: 28px 60px 26px 76px;
  display: block;
  position: relative;

  &:hover .nav__text,
  &:focus .nav__text {
    opacity: 0.6;
  }

  &:active .nav__text {
    opacity: 0.3;
  }

  &:hover .nav__icon {
    fill: @color-main;
    opacity: 1;
  }

  &:active .nav__icon {
    fill: @color-main;
    opacity: 0.3;
  }

  & svg {
    position: absolute;
    left: 32px;
    top: 30px;

    @media (min-width: @tablet-width) {
      top: 36px;
      left: 31px;
    }
  }

  &--delivery {
    .text(15px; 24px; @color-text; normal);
    padding-right: 0;
  }

  &--basket {
    padding-right: 0;
  }

  &--logo {
    padding: 23px 0;

    &:hover,
    &:focus {
      opacity: 0.6;
    }

    &:active {
      opacity: 0.3;
    }
  }

  @media (min-width: @tablet-width) {
    .text(15px, 24px, @color-title, bold);
    padding: 32.5px 36px;
    padding-left: 0;
  }
}

.nav__icon {
  opacity: 0.2;
}

@media (min-width: @tablet-width) {
  .nav-wrapper {
    min-width: 668px;
  }

  .nav__list {
    min-width: 668px;
    display: flex;
    flex-wrap: wrap;
  }

  .nav__item--tablet {
    display: block;
  }

  .nav__item {
    background: transparent;
    box-shadow: none;
    height: 89px;
    order: 10;


    &--logo {
      min-width: 320px;
      width: 48%;
      order: 1;
      margin-right: auto;
    }

    &--search {
      width: 82px;
      order: 2;
      border-left: 1px solid rgba(@color-title, 0.1);
      border-right: 1px solid rgba(@color-title, 0.1);

      & .nav__text {
        font-size: 0;
      }
    }

    &--basket {
      text-align: right;
      min-width: 264px;
      order: 3;
    }

    &--catalog {
      order: 4;
      margin-top: -12px;
    }

    &--order {
      order: 5;
      margin-top: -12px;
    }

    &--delivery {
      order: 6;
      margin-top: -12px;
      margin-left: auto;
    }
  }

  .nav__link--basket {
    .text(17px; 24px; @color-title; bold);
    padding: 33px 0 0 0;
  }

  .nav__link--delivery {
    text-align: left;
    font-weight: normal;
    padding: 32px 0 0 0;
  }

  .nav__link--logo {
    padding: 22px 0;
  }
}

.nav__item--logo {
  @media (min-width: @between-width) {
    width: 54%;
  }
}

@media (min-width: @desktop-width) {
  .nav__list {
    max-width: 1050px;
  }

  .nav__item {
    &--desktop {
      display: block;
      order: 6;
      margin-top: -12px;
    }

    &--logo {
      width: 320px;
      margin-right: -30px;
      order: 3;
    }

    &--search {
      order: 4;
      border-left: 1px solid rgba(@color-title, 0.1);
      border-right: 1px solid rgba(@color-title, 0.1);
    }

    &--basket {
      width: auto;
      order: 5;
    }

    &--catalog {
      margin-top: 0;
      order: 1;
      border-right: 1px solid rgba(@color-title, 0.1);
    }

    &--order {
      margin-top: 0;
      order: 2;
    }

    &--delivery {
      order: 7;
      min-width: 230px;
      text-align: right;
    }
  }

  .nav__link:not(.nav__link--search, .nav__link--delivery, .nav__link--desktop) {
    .text(17px; 24px; @color-title; bold);
  }

  .nav__link--sale {
    padding-left: 27px;
  }

  .nav__link--order {
    padding-left: 35px;
  }

  .nav__link--logo {
    padding: 10px 0;
  }
}
